import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue'),
  },
  {
    path: '/front',
    name: 'Front',
    component: () => import('../views/Front.vue'),
    children: [
      {
        path: 'onlinestore',
        component: () => import('../views/front/Onlinestore.vue'),
        chinese: '線上購物',
        english: 'ONLINE STORE',
        on_header_nav: true,
        children: [
          {
            path: ':category',
            component: () => import('../views/front/Category.vue'),
            chinese: '產品類別',
            english: 'CATEGORY',
          },
        ],
      },
      {
        path: 'onlinestore/:category/:id',
        component: () => import('../views/front/Product.vue'),
        chinese: '產品介紹',
        english: 'PRODUCT',
        on_header_nav: false,
      },
      {
        path: 'news',
        component: () => import('../views/front/News.vue'),
        chinese: '最新消息',
        english: 'NEWS',
        on_header_nav: true,
      },
      {
        path: 'about',
        component: () => import('../views/front/About.vue'),
        chinese: '關於我們',
        english: 'ABOUT',
        on_header_nav: true,
      },
      {
        path: 'orderinfo',
        component: () => import('../views/front/Orderinfo.vue'),
        chinese: '購物指南',
        english: 'Order Info.',
        on_header_nav: true,
      },
      {
        path: 'contact',
        component: () => import('../views/front/Contact.vue'),
        chinese: '連絡我們',
        english: 'CONTACT',
        on_header_nav: true,
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
